import React, { Component } from "react";
import './App.css';
 
class Home extends Component {
  render() {
    return (
      <div>
        <h2 style={{color:"#0E3C5F"}}>Farm Loan Programs</h2>
        <p style={{color: "darkseagreen"}}>The user interface of Loan Servicing, Loan Making or Customer Profile allows a user to locate and select a customer.</p>
      </div>
    );
  }

  componentDidMount() {
    console.log("Component has mountednew!");
    document.getElementById("homelink").classList.add("active");
    document.getElementById("customerslink").classList.remove("active");
    document.getElementById("loanslink").classList.remove("active");
  }

}
 
export default Home;